@import "../../scss/mixins";
.call {
	background-color: rgba($color: $bgBlack, $alpha: 0.6);
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 2000;
	visibility: hidden;

	&.active {
		visibility: visible;
	}
	&__wrapper {
		position: fixed;
		top: -100%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: $bgMain;
		padding: rem(40) rem(45);
		max-width: rem(560);
		transition: top 0.3s ease 0s;

		// height: rem(380);
		@include adaptiveValue("height", 410, 370, 1440, 991.98, 1);
		@include adaptiveValue("padding", 40, 20, 1440, 991.98, 1);
		overflow: auto;

		display: flex;
		flex-direction: column;
		gap: rem(20);

		&.active {
			top: 50%;
		}

		@media (max-width: $mobile) {
			max-width: 100%;
			width: 100%;
			height: 100%;
		}
	}
	&__top {
		display: flex;
		justify-content: flex-end;
	}
	&__text {
		text-align: center;
	}
	&__title {
		@include adaptiveValue("font-size", 28, 22, 767.98, 320, 1);
		font-weight: 600;
		margin-bottom: em(5, 28);
	}
	&__subtitle {
		@include adaptiveValue("font-size", 18, 16, 767.98, 320, 1);
	}
	&__form {
		display: flex;
		flex-direction: column;
		gap: rem(20);
	}
	&__input {
		padding: rem(10) rem(20);
		background-color: rgba($color: $bgBlueDark, $alpha: 0.2);
		border-radius: rem(5);

		font-size: inherit;
		color: $colorMain;
	}
	&__valide {
		font-weight: 500;
		color: $colorRed;
	}
	&__submit {
		cursor: pointer;
		padding: rem(10) rem(20);
		background-color: $bgBlueDark;

		font-size: inherit;
		color: $colorWhite;
		transition: background-color 0.3s ease 0s;
		&:hover {
			background-color: $colorBlue;
		}
	}
}
