@import "../../../scss/mixins";
.products {
	@include adaptiveValue("padding-bottom", 50, 30, 991.98, 320, 1);
	&__container {
	}
	&__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: rem(20);
		@media (max-width: $tablet) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media (max-width: $mobile) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: $mobileSmall) {
			grid-template-columns: 1fr;
			gap: rem(10);
		}
	}
	&__item {
		flex: 1;
		display: flex;
		flex-direction: column;

		@media (any-hover: hover) {
			&:hover {
				.products__image {
					img {
						transform: scale(1.1);
					}
				}
				.products__sign {
					opacity: 1;
				}
				.products__title {
					color: $bgBlueDark;
				}
			}
		}
	}
	&__image {
		width: 100%;
		@include adaptiveValue("height", 300, 200, 1440, 991.98, 1);

		border-radius: rem(20);
		overflow: hidden;
		position: relative;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transition: transform 0.6s ease 0s;
		}
	}
	&__sign {
		opacity: 0;
		position: absolute;
		top: rem(10);
		left: rem(10);
		z-index: 1;
		background-color: $bgBlue;
		border-radius: rem(20);
		color: $colorWhite;
		padding: rem(4) rem(8);
		transition: opacity 0.3s ease 0s;

		@media (max-width: $mobile) {
			opacity: 1;
		}
	}
	&__content {
		flex: 1;
		padding: rem(10) rem(5);
		display: flex;
		flex-direction: column;
		gap: rem(8);
	}
	&__title {
		color: $colorBlue;
		@include adaptiveValue("font-size", 20, 18, 767.98, 320, 1);
		font-weight: 600;
		flex: 1;
		transition: color 0.3s ease 0s;
	}

	&__price {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: rem(10);
	}
	&__discount {
		display: inline-block;
		padding: rem(5) rem(10);
		background-color: $bgBlue;
		color: $colorWhite;
		font-size: rem(18);
		@include adaptiveValue("font-size", 18, 16, 767.98, 320, 1);
		font-weight: 600;
		border-radius: 20px;
	}
	&__strike {
		color: $colorSecondary;
	}
}
