[class*=__container]{max-width:1440px;box-sizing:content-box;margin:0 auto;padding:0 15px}.burger{position:fixed;top:0;left:-100%;z-index:2000;width:50%;height:100vh;box-shadow:0.125rem 0px 0.625rem 0.0625rem rgba(0,0,0,.75);background-color:#0192cb;background:#0192cb;background:linear-gradient(45deg, rgb(1, 146, 203) 0%, rgb(0, 159, 222) 100%);transition:left .3s ease 0s}.burger.active{left:0}@media(max-width: 47.99875em){.burger{width:100%}}.burger__wrapper{display:flex;flex-direction:column;gap:1.25rem;height:100%;padding:1.875rem 0.9375rem 0.9375rem 0.9375rem;overflow-y:auto}@media(min-width: 47.99875em){.burger__wrapper::-webkit-scrollbar{width:10px}.burger__wrapper::-webkit-scrollbar-track{background:#fff}.burger__wrapper::-webkit-scrollbar-thumb{background-color:rgba(124,124,124,.7);border-radius:6px}.burger__wrapper::-webkit-scrollbar-thumb:hover{background-color:#7c7c7c}}.burger__top{display:none;justify-content:flex-end}@media(max-width: 47.99875em){.burger__top{display:flex}}.burger__categories{flex:1}.burger__category{display:flex;flex-direction:column}.burger__category:hover .burger__lists{display:block}.burger__category:hover .burger__button::after{transform:translate(0px, -50%) rotate(180deg)}.burger__button{color:#fff;font-weight:700;font-size:1rem;padding:0.3125rem 1.25rem 0.3125rem 0;text-align:left;position:relative}.burger__button::after{content:"∨";position:absolute;top:50%;right:0;transform:translate(0px, -50%);width:0.625rem;height:1.3125rem;transition:all .3s ease 0s}.burger__lists{display:none;padding:0.625rem 0}.burger__list{padding:0 0.625rem;margin-bottom:0.3571428571em;display:flex}.burger__list a{flex:1;color:#fff;font-size:0.875rem;padding:0.5rem;transition:color .3s ease 0s}.burger__list a:focus,.burger__list a:hover{color:#e5e5e5;font-weight:600}.burger__bottom{display:flex;align-items:center;justify-content:space-between;gap:1.25rem}@media(max-width: 29.99875em){.burger__bottom{flex-direction:column}}.burger__social{display:flex;gap:0.3125rem}.burger__link{color:#009fde;width:2.1875rem;height:2.1875rem;display:flex;justify-content:center;align-items:center;background-color:#fff;border-radius:50%;transition:background-color .3s ease 0s}.burger__link:hover{background-color:rgba(255,255,255,.9)}.burger__call{display:none;color:#009fde;font-size:0.875rem;font-weight:700;background-color:#fff;border-radius:1.25rem;padding:0.625rem 1.25rem;transition:background-color .3s ease 0s}.burger__call:hover{background-color:rgba(255,255,255,.9)}@media(max-width: 47.99875em){.burger__call{display:block}}@media(max-width: 29.99875em){.burger__call{padding:0.625rem 1.875rem}}