@import "../../scss/mixins";
.footer {
	background-color: rgba($color: $bgSecondary, $alpha: 0.6);
	@include adaptiveValue("padding-top", 50, 20, 991.98, 320, 1);
	@include adaptiveValue("padding-bottom", 50, 20, 991.98, 320, 1);
	&__container {
		display: flex;
		flex-direction: column;
		gap: rem(20);
	}
	&__top {
		display: flex;
		gap: rem(20);
		justify-content: space-between;
		@media (max-width: $mobile) {
			flex-direction: column;
			align-items: center;
		}
	}
	&__logo {
		width: rem(300);
		height: rem(72);
		img {
			width: 100%;
			height: 100%;
		}
	}
	&__timetable {
		display: flex;
		gap: rem(20);
		@media (max-width: $mobile) {
			text-align: center;
		}
		// @media (max-width:$mobileSmall){
		// 	 flex-direction: column;
		// }
	}
	&__inner {
		display: flex;
		flex-direction: column;
		gap: rem(8);

		@include adaptiveValue("font-size", 18, 14, 991.98, 320, 1);
		color: $colorSecondary;

		a {
			color: $bgBlue;
			transition: color 0.3s ease 0s;

			&:hover {
				color: $bgBlueDark;
			}
		}
	}
	&__title {
		@include adaptiveValue("font-size", 20, 16, 991.98, 320, 1);
		font-weight: 500;
		color: $colorTernary;
	}
	&__time {
	}
	&__phone {
	}
	&__mail {
	}
	&__bottom {
		@media (max-width: $mobile) {
			text-align: center;
		}
	}
	&__copy {
		font-size: rem(14);
		font-weight: 300;
		color: $colorSecondary;
		&:not(:last-child) {
			margin-bottom: em(5, 14);
		}
	}
	&__policy {
		@include adaptiveValue("font-size", 18, 16, 991.98, 320, 1);
		color: $bgBlue;
		transition: color 0.3s ease 0s;

		&:hover {
			color: $bgBlueDark;
		}
	}
}
