@import "../../scss/mixins";
.scroll {
	cursor: pointer;

	position: fixed;
	z-index: 1000;
	bottom: rem(30);
	right: rem(30);

	@include adaptiveValue("width", 50, 40, 991.98, 767.98, 1);
	@include adaptiveValue("height", 50, 40, 991.98, 767.98, 1);
	background-color: $bgBlueDark;
	border-radius: 50%;

	display: flex;
	justify-content: center;
	align-items: center;

	transition: background-color 0.3s ease 0s;

	@media (max-width: $mobile) {
		display: none;
	}

	&:hover {
		background-color: $bgBlue;
	}
	&__button {
		font-size: rem(20);
		color: $bgMain;
	}
}
