@import "../../scss/mixins";
.close {
	font-size: 0;
	width: rem(28);
	height: rem(20);
	position: relative;

	&::after,
	&::before,
	span {
		content: "";
		display: block;
		width: 100%;
		height: rem(3);
		background-color: $bgMain;
		position: absolute;
		transition:
			transform 0.3s ease 0s,
			width 0.3s ease 0s,
			top 0.3s ease 0s;
	}

	&.blue {
		&::after,
		&::before,
		span {
			background-color: $bgBlueDark;
		}
	}

	&::after {
		top: 50%;
		transform: translateY(-50%) rotate(-45deg);
	}
	&::before {
		top: 50%;
		transform: translateY(-50%) rotate(45deg);
	}
}
